define('DefaultConfigurationOptions',[
		'NamespaceConfig2Ol', 'jquery'
	],
	function (G, $) {
		'use strict';

		var INSTANCE = null;
		var InternalClass = G.core.Class.extend({
			options: {
				projection: 'EPSG:28992',
				units: 'm',
				resolutions: [
					3440.64,
					1720.32,
					860.16,
					430.08,
					215.04,
					107.52,
					53.76,
					26.88,
					13.44,
					6.72,
					3.36,
					1.68,
					0.84,
					0.42,
					0.21
				],
				//left, bottom, right, top
				extent: [
					-285401.920000,
					22598.080000,
					595401.920000,
					903401.920000
				],
				center: {
					x: 155000.0,
					y: 456000.0
				},
				zoom: 3
			},
			initialize: function () {
				//Define properties
				Object.defineProperty(this, 'projection', {
					get: function () {
						return this.options.projection;
					}
				});
				Object.defineProperty(this, 'units', {
					get: function () {
						return this.options.units;
					}
				});
				Object.defineProperty(this, 'resolutions', {
					get: function () {
						return this.options.resolutions;
					}
				});
				Object.defineProperty(this, 'extent', {
					get: function () {
						return this.options.extent;
					}
				});
				Object.defineProperty(this, 'center', {
					get: function () {
						return this.options.center;
					}
				});
				Object.defineProperty(this, 'zoom', {
					get: function () {
						return this.options.zoom;
					}
				});
			},
			// possibility to override default options
			override: function(options) {
				$.extend(this.options, options);
			}
		});

		//Create a singleton object
		G.config2ol.DefaultConfigurationOptions = (function () {
			if (INSTANCE === null) {
				INSTANCE = new InternalClass();
			}
			return INSTANCE;
		})();

		return G;
	});
