define('Configuration',[
		'NamespaceConfig2Ol',
		'openlayers',
		'jquery',
		'gdmap-config',
		'DefaultConfigurationOptions',
		'LayerFactory'
	],
	function (G, OpenLayers, $) {
		'use strict';

		G.config2ol.Configuration = G.core.Class.extend({
			_mapConfig: null,
			_data: null,
			_layers: null,
			_loadPromise: null,

			/**
			 * Constructor of the Configuration object.
			 * @param json Cloud viewer configuration in json format (optional)
			 * Defines the following properties:
			 * data: configuration in raw json format
			 * uid: user id
			 * map: map definition
			 * extensionUri: extensionUri for loading custom viewer content
			 * For more info see http://lingis.geodan.nl/wiki/index.php/CloudViewer#Extensions
			 * groups: defined layer groups
			 * projection: defined map projection
			 * resolutions: defined map resolutions
			 * maxExtent: defined maximum extent
			 * center: defined map center coordinate
			 * zoom: defined initial zoom
			 * layers: defined map layers
			 * baseLayers: defined map base layers
			 * visibleBaseLayers: defined visible map base layers
			 * overlays: defined map base overlays
			 * scalebar: defined scalebar
			 *
			 */
			initialize: function (json) {
				this._data = json || null;

				//Define properties
				Object.defineProperty(this, 'data', {
					get: function () {
						return this._data;
					},
					set: function (value) {
						this._data = value;
					}
				});

				Object.defineProperty(this, 'uid', {
					get: function () {
						return this._data.uid;
					}
				});

				Object.defineProperty(this, 'map', {
					get: function () {
						return this._data.map;
					}
				});

				Object.defineProperty(this, 'extensionUri', {
					get: function () {
						//jscs:disable requireCamelCaseOrUpperCaseIdentifiers
						return this._data.extension_uri;
						//jscs:enable requireCamelCaseOrUpperCaseIdentifiers
					}
				});

				Object.defineProperty(this, 'groups', {
					get: function () {
						return this._data.groups;
					}
				});

				Object.defineProperty(this, 'projection', {
					get: function () {
						return this._data.map.view.projection || G.config2ol.DefaultConfigurationOptions.projection;
					}
				});

				Object.defineProperty(this, 'resolutions', {
					get: function () {
						var resolutions = null;
						if (this._data.map.view.resolutions && this._data.map.view.resolutions.length > 0) {
							resolutions = this._data.map.view.resolutions;
						}
						return resolutions;
					}
				});

				Object.defineProperty(this, 'maxExtent', {
					get: function () {
						var extent = G.config2ol.DefaultConfigurationOptions.extent;
						if (this._data.map.view.extent && this._data.map.view.extent.length === 4) {
							extent = this._data.map.view.extent;
						}
						return extent;
					}
				});

				Object.defineProperty(this, 'visibleExtent', {
					get: function () {
						var extent = null;
						if (this._data.map.view.visibleExtent && this._data.map.view.visibleExtent.length === 4) {
							extent = this._data.map.view.visibleExtent;
						}
						return extent;
					}
				});

				Object.defineProperty(this, 'center', {
					get: function () {
						var centerCoords = G.config2ol.DefaultConfigurationOptions.center;
						if (this._data.map.view.center && this._data.map.view.center.x && this._data.map.view.center.y) {
							centerCoords = this._data.map.view.center;
						}
						return centerCoords;
					}
				});

				Object.defineProperty(this, 'zoom', {
					get: function () {
						return ((typeof this._data.map.view.zoom === 'number') && this._data.map.view.zoom >= 0) ? this._data.map.view.zoom : G.config2ol.DefaultConfigurationOptions.zoom;
					}
				});

				Object.defineProperty(this, 'layers', {
					get: function () {
						return this._data.map.layers;
					}
				});

				Object.defineProperty(this, 'baseLayers', {
					get: function () {
						return $.grep(this._data.map.layers, function (layer) {
							return layer.isBaseLayer;
						});
					}
				});

				Object.defineProperty(this, 'visibleBaseLayers', {
					get: function () {
						return $.grep(this.baseLayers, function (layer) {
							return layer.options.visible;
						});
					}
				});

				Object.defineProperty(this, 'overlays', {
					get: function () {
						return $.grep(this._data.map.layers, function (layer) {
							return !layer.isBaseLayer;
						});
					}
				});

				Object.defineProperty(this, 'scalebar', {
					get: function () {
						return $.grep(this._data.map.controls, function (control) {
							return (control.name === 'scalebar' && (control.active === true || control.active === undefined));
						}).length === 1;
					}
				});

				Object.defineProperty(this, 'measuretool', {
					get: function () {
						var tool =  $.grep(this._data.map.controls, function (control) {
								return control.name === 'measuretool';
							}).length === 1;
						return tool;
					}
				});

				Object.defineProperty(this, 'coordinaten', {
					get: function () {
						var tool =  $.grep(this._data.map.controls, function (control) {
								return (control.name === 'coordinaten' && (control.active === true || control.active === undefined));
							}).length === 1;
						return tool;
					}
				});

			},

			/**
			 * Sets the ExtensionUri
			 * For more info see http://lingis.geodan.nl/wiki/index.php/CloudViewer#Extensions
			 * @param customExtensionUri the Extension uri to use
			 */
			setExtensionUri: function(customExtensionUri) {
				//jscs:disable requireCamelCaseOrUpperCaseIdentifiers
				if (this._data.extension_uri) {
					if (this._data.extension_uri.indexOf('http') !== 0) {
						this._data.extension_uri = customExtensionUri + this._data.extension_uri;
					}
				}
				//jscs:enable requireCamelCaseOrUpperCaseIdentifiers
			},

			/**
			 * Load a configuration stored in the document service
			 * More information about the json configuration can be found here: http://lingis.geodan.nl/wiki/index.php/JSONCloudViewer
			 * More information about the document service can be found here: http://lingis.geodan.nl/wiki/index.php/Document_service
			 *
			 * @param baseurl The base url of the document service
			 * @param account The organisation code to use for the document service
			 * @param serviceName The name of the service the configuration belongs to
			 * @param configName The name of the configuration to use
			 * @param options Particular options for the function to execute properly:
			 * context: the context to be used when handling the calllbacks from the promise
			 * converterOptions: options passed on to the converterObject for example { urlRoot: "https://ontw.geodan.nl" } to allow the config version schema to be received from different location during development
			 * @returns { a promise which handles loading of the specified configuration }
			 */
			load: function (baseurl, account, serviceName, configName, options) {
				options = options || {};
				this._data = null;
				this._loadPromise = $.Deferred();
				var configManager = new G.config.ConfigManager(baseurl, account, serviceName, options.converterOptions);
				var configOptions = {
					context: this
				};
				configManager.getConfigData(configName, '1.0.0', configOptions).then(this._loadResolved, this._loadRejected);
				return this._loadPromise;
			},

			_loadResolved: function(data) {
				this._data = data;
				this._loadPromise.resolve();
			},

			_loadRejected: function() {
				console.log('failed to load config from document service');
				this._loadPromise.reject();
			}
		});

		return G;
	});

