define('tools/IsochroneTool',[
		'NamespaceConfig2Ol'
	],
	function (G) {
		'use strict';

		G.config2ol.tools.IsochroneTool = G.core.Class.extend({
			initialize: function (serviceUrl) {
				this._serviceUrl = serviceUrl;
				this._requestManager = new G.core.RequestManager();
			},

			calculateIsochrone: function (x, y, srs, fromTo, calcSize, calcMode) {
				var urlParams = this._createUrlParams(x, y, srs, fromTo, calcSize, calcMode);
				var url = encodeURI(this._serviceUrl + urlParams);
				return this._requestManager.get(url);
			},

			_createUrlParams: function (x, y, srs, fromTo, calcSize, calcMode) {
				//Get right calculation settings
				switch (calcMode) {
					case 'distance':
						calcSize = calcSize ? calcSize : 5; // within 5 km from location
						break;
					case 'cost':
						calcSize = calcSize ? calcSize : 20; // within a distance from location that costs max 20 euros to reach
						break;
					default:
						calcSize = calcSize ? calcSize : 10; // within 10 minutes from location
						calcMode = 'time';
						break;
				}

				//Create params
				var params = '';
				if (fromTo === 'from') {
					params += 'fromcoordx=' + x + '&fromcoordy=' + y;
				} else {
					params += 'tocoordx=' + x + '&tocoordy=' + y;
				}

				params += '&srs=' + srs;
				params += '&calcmode=' + calcMode;
				params += '&calcsize=' + calcSize;
				params += '&outputformat=json';

				return params;
			}
		});

		return G;
	});
