define('Map',[
		'NamespaceConfig2Ol',
		'openlayers',
		'jquery',
		'Configuration',
		'Control'
	],
	function (G, OpenLayers, $) {
		'use strict';

		G.config2ol.Map = G.core.Class.extend({
			_elementId: null,
			_configuration: null,
			_map: null,
			_layers: null,
			_controls: null,
			_initPromise: null,

			/**
			 * Constructor of the Map class. Defines the following properties:
			 * configuration: property containing the configuration object
			 * map:  property containing the OpenLayers map object
			 * layers: property containing the OpenLayers layers
			 * baseLayers: property containing the OpenLayers baselayers
			 * visibleBaseLayers: property containing the visible OpenLayers baselayers
			 * overlays: property containing the OpenLayers overlays
			 * @param elementId HTML div element id to be used as a container for the map
			 */
			initialize: function (elementId) {
				this._elementId = elementId;
				this._configuration = null;
				this._map = null;
				this._controls = {};
				this._initPromise = null;

				//Define properties
				Object.defineProperty(this, 'configuration', {
					get: function () {
						return this._configuration;
					}
				});
				Object.defineProperty(this, 'map', {
					get: function () {
						return this._map;
					}
				});
				Object.defineProperty(this, 'layers', {
					get: function () {
						return this._layers;
					}
				});
				Object.defineProperty(this, 'baseLayers', {
					get: function () {
						return $.grep(this._layers, function (layer) {
							return layer.isBaseLayer;
						});
					}
				});

				Object.defineProperty(this, 'visibleBaseLayers', {
					get: function () {
						return $.grep(this.baseLayers, function (layer) {
							return layer.visibility;
						});
					}
				});

				Object.defineProperty(this, 'overlays', {
					get: function () {
						return $.grep(this._layers, function (layer) {
							return !layer.isBaseLayer;
						});
					}
				});

				/**
				 * Return the controls created by this API.
				 * This property is an object where each control is placed in a property.
				 * The possible names of the properties are defined in G.config2ol.ControlNames
				 * For instance, to retrieve the scalebar ask for this.controls[G.config2ol.ControlNames.scalebar]
				 */
				Object.defineProperty(this, 'controls', {
					get: function () {
						return this._controls;
					}
				});
			},

			/***
			 * Sets the center of the map to the center coordinate defined in the configuration
			 */
			setCenter: function() {
				this.map.setCenter(new OpenLayers.LonLat(this._configuration.getCenterX(), this._configuration.getCenterY()), this._configuration.getZoomlevel());
			},

			/**
			 * Creates an OpenLayers map from a json configuration stored in the document service.
			 * More information about the json configuration can be found here: http://lingis.geodan.nl/wiki/index.php/JSONCloudViewer
			 * More information about the document service can be found here: http://lingis.geodan.nl/wiki/index.php/Document_service
			 *
			 * @param baseurl The base url of the document service
			 * @param account The organisation code to use for the document service
			 * @param serviceName The name of the service the configuration belongs to
			 * @param configName The name of the configuration to use
			 * @param options Particular options for the function to execute properly:
			 * context: the context to be used when handling the calllbacks from the promise
			 * @returns { a promise which handles creating of the OpenLayers map }
			 */
			createMapFromConfiguration: function (baseurl, account, serviceName, configName, options) {
				var self = this;
				options = options || {};
				var context = options.context || this;
				this._initPromise = $.Deferred();

				this._configuration = new G.config2ol.Configuration();
				this._configuration.load(baseurl, account, serviceName, configName, options).then(function () {
					try {
						self._createMap(options);
						self._initPromise.resolveWith(context, [this]);
					}
					catch (error) {
						console.log('Failed to create map from configuration: ' + error);
						self._destroyMap();
						self._initPromise.rejectWith(context);
					}
				}, function (error) {
					console.log('Error loading the configuration: ' + error);
					self._initPromise.rejectWith(context);
				});

				return this._initPromise;
			},

			/**
			 * Creates an OpenLayers map from a json configuration.
			 * More information about the json configuration can be found here: http://lingis.geodan.nl/wiki/index.php/JSONCloudViewer
			 * More information about the document service can be found here: http://lingis.geodan.nl/wiki/index.php/Document_service
			 *
			 * @param json Cloud viewer configuration in json format
			 * @returns {a promise which handles loading of the specified configuration}
			 */
			createMapFromJSON: function (json) {
				this._configuration = new G.config2ol.Configuration(json);

				this._initPromise = $.Deferred();
				this._createMap({});
				this._initPromise.resolve(this);
				return this._initPromise;
			},

			/**
			 * Removes layers from the map. When there's not a single base layer present any longer,
			 * a new empty base layer will be added.
			 * @param layerNames An array containing names of layers to remove
			 */
			removeLayers: function(layerNames) {
				for (var i = 0; i < layerNames.length; i++) {
					var layers = this._map.getLayersByName(layerNames[i]);
					for (var j = 0; j < layers.length; j++) {
						this._removeLayer(layers[j]);
					}
				}
				if (this.baseLayers.length === 0) {
					var layer = G.config2ol.LayerFactory.createEmptyLayer({title:'empty'});
					this._layers.push(layer);
					this._map.addLayers(this.baseLayers);
					this._map.setBaseLayer(this.visibleBaseLayers[0]);
				}
			},

			_removeLayer: function(layer) {
				var pos = this._layers.indexOf(layer);
				if (pos >= 0) {
					this._layers.splice(pos, 1);
					this._map.removeLayer(layer, false);
				}
			},

			_parseLayers: function () {
				var layer;
				this._layers = [];
				var data = this._configuration.data;
				for (var i = 0; i < data.map.layers.length; ++i) {
					layer = G.config2ol.LayerFactory.create(data.uid, data.map.layers[i]);
					if (layer !== null) {
						this._layers.push(layer);
					}
				}
				if (this.baseLayers.length === 0) {
					layer = G.config2ol.LayerFactory.createEmptyLayer({title:'empty'});
					this._layers.push(layer);
				}

				this._map.addLayers(this.overlays);

				var visibleBaseLayers = this.visibleBaseLayers;
				if (visibleBaseLayers.length > 0) {
					this._map.addLayer(visibleBaseLayers[0]);
					this._map.setBaseLayer(visibleBaseLayers[0]);
				}
				this._map.addLayers(this.baseLayers);
			},

			_setNumZoomLevels: function() {
				if (this._map.resolutions && this.map.resolutions.length > 0) {
					this._map.numZoomLevels = this.map.resolutions.length;
				}
			},

			_createMap: function (options) {
				this._map = new OpenLayers.Map({
					div: this._elementId,
					theme: null,
					projection: this._configuration.projection,
					resolutions: this._configuration.resolutions,
					maxExtent: this._configuration.maxExtent,
					restrictedExtent: this._configuration.visibleExtent,
					center: this._configuration.center,
					zoom: this._configuration.zoom,
					units: 'm'
				});

				this._setNumZoomLevels();

				this._parseLayers();

				if (this._configuration.scalebar) {
					var scaleBar = new G.config2ol.Control(G.config2ol.ControlNames.scalebar);
					this._map.addControl(scaleBar.control);
					this._controls[G.config2ol.ControlNames.scalebar] = scaleBar;
				}

				if (this._configuration.measuretool) {
					var measureTool = new G.config2ol.Control(G.config2ol.ControlNames.measuretool, options);
					this._map.addControl(measureTool.control);
					this._controls[G.config2ol.ControlNames.measuretool] = measureTool;
				}

				if (this._configuration.coordinaten) {
					var coordinaten = new G.config2ol.Control(G.config2ol.ControlNames.coordinaten, options);
					this._map.addControl(coordinaten.control);
					this._controls[G.config2ol.ControlNames.coordinaten] = coordinaten;
				}
			},

			_destroyMap: function () {
				if (this._map !== null) {
					this._map.destroy();
				}
			}
		});

		return G;
	});
