define('controls/Isochrone',[
		'tools/IsochroneTool',
		'openlayers'
	],
	function (G,
			  OpenLayers) {
		'use strict';

		OpenLayers.Control.Isochrone = OpenLayers.Class(OpenLayers.Control, {
			layer: null,

			initialize: function (serviceUrl, layer, options) {
				OpenLayers.Control.prototype.initialize.apply(this, [options]);
				var handlerOptions = {
					single: true,
					double: false,
					pixelTolerance: 0,
					stopSingle: false,
					stopDouble: false
				};

				this.handler = new OpenLayers.Handler.Click(this, {click: this.onClick}, handlerOptions);
				this.layer = layer;

				this._tool = new G.config2ol.tools.IsochroneTool(serviceUrl);
			},

			onClick: function (evt) {
				var lonlat = this.map.getLonLatFromPixel(evt.xy);
				var clickpointVector = new OpenLayers.Feature.Vector(new OpenLayers.Geometry.Point(lonlat.lon, lonlat.lat));

				this.layer.destroyFeatures();
				this.layer.addFeatures(clickpointVector);

				this.events.triggerEvent('calculationStarted', {
					object: this,
					lonlat: lonlat
				});

				var self = this;
				//TODO: change 10 to input value from GUI
				this._tool.calculateIsochrone(lonlat.lon, lonlat.lat, this.map.projection, 'from', 10, 'time')
					.done(function (data, status, jqxhr) {
						var format = new OpenLayers.Format.GeoJSON();
						self.layer.addFeatures(format.read(data));

						self.events.triggerEvent('calculationFinished', {
							object: self,
							geojson: data
						});
					})
					.fail(function (jqxhr, status, error) {
						self.events.triggerEvent('calculationError', {
							object: self,
							error: error
						});
					});
			},

			CLASS_NAME: 'OpenLayers.Control.Isochrone'
		});
	});
