define('LayerFactory',['NamespaceConfig2Ol', 'openlayers'],
	function (G, OpenLayers) {
		'use strict';

		var INSTANCE = null;
		var InternalClass = G.core.Class.extend({

			/**
			 * Constructor of the LayerFactory object.
			 * Initialises the known layer types.
			 * Known layer types are:
			 * OGC WMS
			 * OGC WMTS
			 * OGC TMS
			 * OGC WFS
			 * GeoJSON
			 * TopoJSON
			 * Empty baselayer
			 */
			initialize: function () {
				this.LAYERTYPES = {
					OGC_WMS: 'OGC_WMS',
					OGC_WMTS: 'OGC_WMTS',
					OGC_TMS: 'OGC_TMS',
					GEOJSON: 'GEOJSON',
					TOPOJSON: 'TOPOJSON',
					OGC_WFS: 'OGC_WFS',
					EMPTY: 'EMPTY'
				};
			},

			/**
			 * Create an OpenLayers layer
			 * @param uid User id of a layer served by Geoserver.nl
			 * @param layerConfig Layer configuration part
			 * @returns {OpenLayers layer object}
			 */
			create: function (uid, layerConfig) {
				var layer = null;
				if (layerConfig.source && layerConfig.source.type) {
					switch (layerConfig.source.type.toUpperCase()) {
						case this.LAYERTYPES.OGC_WMTS:
							layer = this._createWMTSLayer(uid, layerConfig);
							break;
						case this.LAYERTYPES.OGC_WMS:
							layer = this._createWMSLayer(uid, layerConfig);
							break;
						case this.LAYERTYPES.OGC_TMS:
							layer = this._createTMSLayer(uid, layerConfig);
							break;
						case this.LAYERTYPES.GEOJSON:
							layer = this._createGeojsonLayer(layerConfig);
							break;
						case this.LAYERTYPES.TOPOJSON:
							layer = this._createTopojsonLayer(layerConfig);
							break;
						case this.LAYERTYPES.OGC_WFS:
							layer = this._createWFSLayer(layerConfig);
							break;
						case this.LAYERTYPES.EMPTY:
							layer = this.createEmptyLayer(layerConfig);
							break;
						default:
							console.log('Map type: "' + layerConfig.source.type + '" is not supported.');
							break;
					}
				} else {
					console.log('Layer source is not correctly defined');
				}
				if (layer) {
					layer.id = layerConfig.id;
				}
				return layer;
			},

			_createWMTSLayer: function (uid, config) {
				return new OpenLayers.Layer.WMTS({
					name: config.title,
					url: this._constructWmtsUrl(config),
					layer: config.source.featureName,
					matrixSet: config.source.matrixset,
					matrixIds: config.source.matrixids,
					format: config.source.contenttype,
					isBaseLayer: config.isBaseLayer,
					transitionEffect: config.source.transitioneffect || 'resize',
					opacity: config.options.opacity,
					transparent: config.options.transparent,
					visibility: config.options.visible,
					style: config.source.style || 'default',
					requestEncoding: config.source.requestencoding || 'REST',
					dimensions: ['uid'],
					params: {uid: uid || ''}
				});
			},

			_createWMSLayer: function (uid, config) {
				var layer = new OpenLayers.Layer.WMS(
					config.title,
					config.source.url,
					{
						layers: config.source.featureName,
						format: config.source.contenttype,
						exceptions: 'application/vnd.ogc.se_inimage',
						transparent: config.options.transparent,
						version: config.source.version,
						uid: uid || ''
					},
					{
						opacity: config.options.opacity,
						buffer: 1,
						isBaseLayer: config.isBaseLayer,
						visibility: config.options.visible
					}
				);
				this._addSldParam(layer, config.style);
				return layer;
			},

			_createTMSLayer: function (uid, config) {
				return new OpenLayers.Layer.TMS(
					config.title,
					config.source.url,
					{
						layername: config.source.featureName,
						type: config.source.contenttype.split('/', 2)[1] + '?uid=' + uid || '',
						isBaseLayer: config.isBaseLayer,
						opacity: config.options.opacity,
						transparent: config.options.transparent,
						visibility: config.options.visible
					}
				);
			},

			_createGeojsonLayer: function (config) {
				var refreshRate = config.options.refreshRate * 1000;
				var strategies = [new OpenLayers.Strategy.Fixed()];
				if (refreshRate > 0) {
					var s = new OpenLayers.Strategy.Refresh({interval: refreshRate});
					strategies.push(s);
				}
				return new OpenLayers.Layer.Vector(
					config.title,
					{
						strategies: strategies,
						protocol: new OpenLayers.Protocol.HTTP({
							url: config.source.url,
							format: new OpenLayers.Format.GeoJSON()
						}),
						opacity: config.options.opacity,
						visibility: config.options.visible
					}
				);
			},

			_createTopojsonLayer: function (config) {
				return null;
			},

			_createWFSLayer: function (config) {
				var refreshRate = config.options.refreshRate * 1000;
				var strategies = [new OpenLayers.Strategy.BBOX()];
				if (refreshRate > 0) {
					var s = new OpenLayers.Strategy.Refresh({interval: refreshRate});
					strategies.push(s);
				}
				return new OpenLayers.Layer.Vector(
					config.title,
					{
						strategies: strategies,
						styleMap: new OpenLayers.StyleMap(config.options.style),
						protocol: new OpenLayers.Protocol.WFS({
							url: config.source.url,
							version: config.source.version,
							featureType: config.source.featureName,
							featureNS: config.source.featureNS,
							srsName: config.source.srsName,
							geometryName: config.source.geometryName
						}),
						opacity: config.options.opacity,
						visibility: config.options.visible
					}
				);
			},

			_constructWmtsUrl: function (layerConfig) {
				var requestEncoding = layerConfig.source.requestencoding || 'REST';
				var url = layerConfig.source.url;
				if (requestEncoding.toUpperCase() === 'REST') {
					var formatExtension = layerConfig.source.contenttype.split('/', 2)[1];
					url = (layerConfig.source.resturl || url + '/' + layerConfig.source.featureName + '/{TileMatrixSet}/{TileMatrix}/{TileCol}/{TileRow}' + '.' + formatExtension) + '?{uid}';
				}
				return url;
			},

			_addSldParam: function (layer, styleConfig) {
				if (styleConfig && styleConfig.url) {
					var sld = {sld:styleConfig.url};
					OpenLayers.Util.extend(layer.params, sld);
				}
			},

			/**
			 * Creates an empty OpenLayers baselayer
			 * @param layerConfig Layer configuration part
			 * @returns {Empty OpenLayers baselayer object}
			 */
			createEmptyLayer: function (layerConfig) {
				layerConfig.id = layerConfig.id || 999999;
				layerConfig.isBaseLayer = true;
				layerConfig.options = layerConfig.options || {};
				layerConfig.options.opacity = 1;
				layerConfig.options.visible = 'true';
				return new OpenLayers.Layer(layerConfig.title, {
					isBaseLayer: layerConfig.isBaseLayer,
					visibility: layerConfig.options.visible
				});
			}
		});

		//Create a singleton object
		G.config2ol.LayerFactory = (function () {
			if (INSTANCE === null) {
				INSTANCE = new InternalClass();
			}
			return INSTANCE;
		})();

		return G;
	});
