define('Control',[
		'ControlNames',
		'openlayers',
		'controls/Isochrone'
	],
	function (G, OpenLayers) {
		'use strict';

		G.config2ol.Control = G.core.Class.extend({
			_name: null,
			_control: null,

			initialize: function (name, options) {

				//Define properties
				Object.defineProperty(this, 'control', {
					get: function () {
						return this._control;
					}
				});

				options = options || {};
				options.measureElement = options.measureElement || 'measure_output';
				var divMeasure = document.getElementById(options.measureElement);
				if (divMeasure === null) {
					divMeasure = document.createElement('div');
					divMeasure.setAttribute('id', options.measureElement);
				}

				this._name = name;
				switch (name) {
					case G.config2ol.ControlNames.scalebar:
						this._control = new OpenLayers.Control.ScaleLine({
								bottomOutUnits: '',
								geodesic: true
							}
						);
						break;
					case G.config2ol.ControlNames.measuretool:
						var handleMeasurements = function (event) {
							var geometry = event.geometry;
							var units = event.units;
							var order = event.order;
							var measure = event.measure;
							var element = divMeasure;
							var out = '';
							if (order === 1) {
								out += measure.toFixed(3) + ' ' + units;
							} else {
								out += measure.toFixed(3) + ' ' + units + '<sup>2</' + 'sup>';
							}
							element.innerHTML = out;
						};

						// style the sketch fancy
						var sketchSymbolizers = {
							Point: {
								pointRadius: 4,
								graphicName: 'square',
								fillColor: 'white',
								fillOpacity: 1,
								strokeWidth: 1,
								strokeOpacity: 1,
								strokeColor: '#333333'
							},
							Line: {
								strokeWidth: 3,
								strokeOpacity: 1,
								strokeColor: '#666666',
								strokeDashstyle: 'dash'
							},
							Polygon: {
								strokeWidth: 2,
								strokeOpacity: 1,
								strokeColor: '#666666',
								fillColor: 'white',
								fillOpacity: 0.3
							}
						};

						var style = new OpenLayers.Style();
						style.addRules([
							new OpenLayers.Rule({symbolizer: sketchSymbolizers})
						]);
						var styleMap = new OpenLayers.StyleMap({default: style});

						// allow testing of specific renderers via '?renderer=Canvas', etc
						var renderer = OpenLayers.Util.getParameters(window.location.href).renderer;
						renderer = (renderer) ? [renderer] : OpenLayers.Layer.Vector.prototype.renderers;

						this._control = new OpenLayers.Control.Measure(
							OpenLayers.Handler.Path, {
								geodesic: true,
								persist: true,
								handlerOptions: {
									layerOptions: {
										renderers: renderer,
										styleMap: styleMap
									}
								}
							});

						this._control.events.on({
							measure: handleMeasurements,
							measurepartial: handleMeasurements
						});
						break;
					case G.config2ol.ControlNames.isochronetool:
						//TODO: get url and layer from outside the library
						var url = '';
						var layer = '';
						this._control = new OpenLayers.Control.Isochrone(url, layer, options);
						break;
					case G.config2ol.ControlNames.coordinaten:
						this._control = new OpenLayers.Control.MousePosition({
							prefix: '',
							separator: ' | ',
							numDigits: 0,
							emptyString: ''
						});
						break;
				}

			}

		});

		return G;
	});

